.total-holder {
  background-color: #282c34;
}

.Image-holder {
    position: relative;
    top: 0;
    left: 0;
    min-height: 100vh; 
    text-align: center;
    background-color: #282c34;
    padding: 10vh;
  }
  
  .Image-positioner {
    position: absolute;
    left: 50%;
    top: 10vh;
    bottom: 10vh;
  }
  
  .img {
    width: 60vw;
    position: absolute;
    top: 0;
    left: 50%;
  }

  .img-2 {
    height: 100vh;
    width: auto;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(-50%);
    opacity: 1.0;
    transition: opacity 0.4s cubic-bezier(.6,.03,.98,.58);
  }
  
  #manip2 {
    /* animation: flash infinite 1s cubic-bezier(.6,.03,.98,.58) alternate; */
    opacity: 0.0
  }
  
  @keyframes flash {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }