.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Image-holder {
  position: relative;
  top: 0;
  left: 0;
  min-height: 100vh; 
  text-align: center;
  background-color: #282c34;
}

.Image-positioner {
  position: absolute;
  left: 20vw;
}

.img {
  width: 60vw;
  position: absolute;
  top: 0;
  left: 0
}

#manip {
  animation: flash infinite 1s cubic-bezier(.6,.03,.98,.58) alternate;
}

.btn-holder {
  background-color: #282c34;
  text-align: center;
}

.btn {
  text-align: center;
  border: none;
  width: 5em;
  height: 2em;
  border-radius: 1em;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: red;
  margin: 1em;
}

@keyframes flash {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
